html,body{
  margin:0;
  padding:0;
}
.clearfix:after{
  content: '';
  display: block;
  height:0;
  clear:both;
}
#app {
  width: 1200px;
  min-height: 900px;
  margin: 0 auto;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: -webkit-linear-gradient(white, #a9dbfa);
  text-align: center;
  color: #2c3e50;
  /*margin-top: 60px;*/
  -webkit-box-shadow: 0 0 5px blue;
          box-shadow: 0 0 5px blue;
  position: relative;
}
/*title navbar routerview 淡入效果*/
.componentFadeIn {
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}
.beforeStart {
  -webkit-transform: translateY(-50px);
          transform: translateY(-50px);
  opacity: 0;
}
.rollingCircle {
  position: absolute;
  width: 60px;
  height: 60px;
  float: left;
  margin-top: 10px;
  left: 20px;
  bottom: 20px;
}
.circle {
  position: absolute;
  top: 30px;
  left: 30px;
  border: 4px solid transparent;
}
.circle-sm {
  width: 16px;
  height: 16px;
  margin-left: -12px;
  margin-top: -12px;
  background: transparent;
  border-radius: 12px;
  border-left: 4px solid #e9e92d;
  border-top: 4px solid #e9e92d;
  border-bottom: 4px solid #e9e92d;
  -webkit-animation: load 3s linear infinite;
          animation: load 3s linear infinite;
}
.circle-md {
  width: 30px;
  height: 30px;
  margin-left: -19px;
  margin-top: -19px;
  background: transparent;
  border-radius: 19px;
  border-top: 4px solid orange;
  border-right: 4px solid orange;
  -webkit-animation: load2 2.5s linear infinite;
          animation: load2 2.5s linear infinite;
}
.circle-lg {
  width: 44px;
  height: 44px;
  margin-left: -26px;
  margin-top: -26px;
  background: transparent;
  border-radius: 26px;
  border-bottom: 4px solid orange;
  border-top: 4px solid orange;
  -webkit-animation: load 3.5s linear infinite;
          animation: load 3.5s linear infinite;
}
@-webkit-keyframes load {
from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@keyframes load {
from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@-webkit-keyframes load2 {
from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
to {
    -webkit-transform: rotate(-360deg);
            transform: rotate(-360deg);
}
}
@keyframes load2 {
from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
to {
    -webkit-transform: rotate(-360deg);
            transform: rotate(-360deg);
}
}

